(function() {
  'use strict';

  angular
    .module('yavi')
    .factory('yaviServer', factoryFunction);

  /** @ngInject */
  function factoryFunction($http, yaviConfig, $interpolate) {

    var factory = {};

    factory.getPageSignal = function(signalType, pageId, dateFrom, dateTo) {
      return $http
        .jsonp(buildPageSignalQuery(signalType, pageId, dateFrom, dateTo))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (angular.isDefined(response)
            && angular.isDefined(response.data)) {
          return response.data;
        }
        onError();
      }

      function onError() {
        throwYaviServerError("Signal could not be retrieved");
      }
    }

    factory.getPageRelatedPages = function(pageId, dateFrom, dateTo) {
      return $http
        .jsonp(buildPageRelatedPagesQuery(pageId, dateFrom, dateTo))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (angular.isDefined(response)
            && angular.isDefined(response.data)) {
          return response.data;
        }
        onError();
      }

      function onError() {
        throwYaviServerError("RelatedPages could not be retrieved");
      }
    }

    return factory;

    function buildPageSignalQuery(signalType, pageId, dateFrom, dateTo) {
      var queryTemplate = '';
      queryTemplate += 'http://localhost:8080/yavi-server/signal?';
      queryTemplate += '&pageid={{pageId}}';
      queryTemplate += '&wikipediaid={{wikipediaId}}';
      queryTemplate += '&datefrom={{dateFrom}}';
      queryTemplate += '&dateto={{dateTo}}';
      queryTemplate += '&signaltype={{signalType}}';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        signalType: signalType,
        pageId: pageId,
        wikipediaId: yaviConfig.wikipediaId,
        dateFrom: dateFrom,
        dateTo: dateTo
      });
    }
    
    function buildPageRelatedPagesQuery(pageId, dateFrom, dateTo) {
      var queryTemplate = '';
      queryTemplate += 'http://localhost:8080/yavi-server/relatedpages?';
      queryTemplate += '&pageid={{pageId}}';
      queryTemplate += '&wikipediaid={{wikipediaId}}';
      queryTemplate += '&datefrom={{dateFrom}}';
      queryTemplate += '&dateto={{dateTo}}';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        pageId: pageId,
        wikipediaId: yaviConfig.wikipediaId,
        dateFrom: dateFrom,
        dateTo: dateTo
      });
    }

    function throwYaviServerError(errorMessage) {
      throw $interpolate("Yavi Server: {{errorMessage}}.")({errorMessage: errorMessage});
    }
  }
  
})();

(function() {
  'use strict';

  angular
    .module('yavi')
    .factory('wikipediaPages', factoryFunction);

  /** @ngInject */
  function factoryFunction(WikipediaPage) {
    var pages = {};

    return new function() {
      this.isValidId = function(pageId) {
        var pageIdInteger = parseInt(pageId, 10);
        return pageIdInteger > 0;
      }

      this.getPageById = function(pageIdArg) {
        var pageId = parseInt(pageIdArg, 10);
        if (!pages.hasOwnProperty(pageId)) {
          pages[pageId] = new WikipediaPage(pageId);
        }
        return pages[pageId];
      }
    }
  }
  
})();

(function() {
  'use strict';

  angular
    .module('yavi')
    .service('WikipediaPage', serviceFunction);

  /** @ngInject */
  function serviceFunction($q, wikipediaAPI, yaviServer) {
    return WikipediaPage;

    function WikipediaPage(pageId) {

      var self = this;

      self.pageId = pageId;

      // Page properties.
      var categoryList = undefined;
      var description = undefined;
      var imagesList = undefined;
      var numberOfAddedInlinksList = undefined;
      var numberOfAddedOutlinksList = undefined;
      var numberOfRevertedRevisionsList = undefined;
      var numberOfRevisionsList = undefined;
      var numberOfTotalOutlinksList = undefined;
      var numberOfUniqueEditorsList = undefined;
      var pageContentSizeList = undefined;
      var relatedPages = undefined;
      var thumbnail = undefined;
      var title = undefined;

      // Property promise getters.
      self.getCategoryList = function() {
        if (angular.isUndefined(categoryList)) {
          return wikipediaAPI.getPageCategories(pageId)
              .then(function(val) {
                categoryList = val;
                return categoryList;
              });
        }
        return $q.when(categoryList);
      }

      self.getDescription = function() {
        if (angular.isUndefined(description)) {
          return wikipediaAPI.getPageDescription(pageId)
              .then(function(val) {
                description = val;
                return description;
              });
        }
        return $q.when(description);
      }

      self.getImagesList = function() {
        if (angular.isUndefined(imagesList)) {
          return wikipediaAPI.getPageImagesList(pageId)
              .then(function(val) {
                imagesList = val;
                return imagesList;
              });
        }
        return $q.when(imagesList);
      }

      self.getNumberOfAddedInlinksList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(numberOfAddedInlinksList)) {
        return yaviServer.getPageSignal("numberofaddedinlinks", pageId, dateFrom, dateTo)
            .then(function(val) {
              numberOfAddedInlinksList = val;
              return numberOfAddedInlinksList;
            });
        }
        return $q.when(numberOfAddedInlinksList);
      }

      self.getNumberOfAddedOutlinksList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(numberOfAddedOutlinksList)) {
        return yaviServer.getPageSignal("numberofaddedoutlinks", pageId, dateFrom, dateTo)
            .then(function(val) {
              numberOfAddedOutlinksList = val;
              return numberOfAddedOutlinksList;
            });
        }
        return $q.when(numberOfAddedOutlinksList);
      }

      self.getNumberOfRevertedRevisionsList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(numberOfRevertedRevisionsList)) {
        return yaviServer.getPageSignal("numberofrevertedrevisions", pageId, dateFrom, dateTo)
            .then(function(val) {
              numberOfRevertedRevisionsList = val;
              return numberOfRevertedRevisionsList;
            });
        }
        return $q.when(numberOfRevertedRevisionsList);
      }

      self.getNumberOfRevisionsList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(numberOfRevisionsList)) {
        return yaviServer.getPageSignal("numberofrevisions", pageId, dateFrom, dateTo)
            .then(function(val) {
              numberOfRevisionsList = val;
              return numberOfRevisionsList;
            });
        }
        return $q.when(numberOfRevisionsList);
      }

      self.getNumberOfTotalOutlinksList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(numberOfTotalOutlinksList)) {
        return yaviServer.getPageSignal("numberoftotaloutlinks", pageId, dateFrom, dateTo)
            .then(function(val) {
              numberOfTotalOutlinksList = val;
              return numberOfTotalOutlinksList;
            });
        }
        return $q.when(numberOfTotalOutlinksList);
      }

      self.getNumberOfUniqueEditorsList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(numberOfUniqueEditorsList)) {
        return yaviServer.getPageSignal("numberofuniqueeditors", pageId, dateFrom, dateTo)
            .then(function(val) {
              numberOfUniqueEditorsList = val;
              return numberOfUniqueEditorsList;
            });
        }
        return $q.when(numberOfUniqueEditorsList);
      }

      self.getPageContentSizeList = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(pageContentSizeList)) {
        return yaviServer.getPageSignal("pagecontentsize", pageId, dateFrom, dateTo)
            .then(function(val) {
              pageContentSizeList = val;
              return pageContentSizeList;
            });
        }
        return $q.when(pageContentSizeList);
      }

      self.getRelatedPages = function(dateFrom, dateTo) {
        if (true || angular.isUndefined(relatedPages)) {
          return yaviServer.getPageRelatedPages(pageId, dateFrom, dateTo)
              .then(function(val) {
                relatedPages = val;
                return relatedPages;
              });
        }
        return $q.when(relatedPages);
      }

      self.getThumbnail = function() {
        if (angular.isUndefined(thumbnail)) {
          return wikipediaAPI.getPageThumbnail(pageId)
              .then(function(val) {
                thumbnail = val;
                return thumbnail;
              });
        }
        return $q.when(thumbnail);
      }

      self.getTitle = function() {
        if (angular.isUndefined(title)) {
          return wikipediaAPI.getPageTitle(pageId)
              .then(function(val) {
                title = val;
                return title;
              });
        }
        return $q.when(title);
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('yavi')
    .factory('wikipediaAPI', factoryFunction);

  /** @ngInject */
  function factoryFunction($http, yaviConfig, $q, $interpolate) {

    var factory = {};

    factory.getPageCategories = function(pageId) {
      return $http
        .jsonp(buildPageCategoriesQuery(pageId))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (response
            && response.data
            && response.data.query
            && response.data.query.pages
            && response.data.query.pages[pageId]
            && response.data.query.pages[pageId].categories) {
          var categories = response.data.query.pages[pageId].categories;
          var categoryTitles = [];
          angular.forEach(categories, function(category) {
            var categoryTitle = category.title;
            var categoryTitleNoPrefix =
                categoryTitle.substr(categoryTitle.indexOf(':') + 1, categoryTitle.length);
            this.push(categoryTitleNoPrefix);
          }, categoryTitles);
          return categoryTitles;
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Categories could not be retrieved");
      }
    }

    factory.getPageDescription = function(pageId) {
      return $http
        .jsonp(buildPageBasicDataQuery(pageId))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (response
            && response.data
            && response.data.query
            && response.data.query.pages
            && response.data.query.pages[pageId]
            && response.data.query.pages[pageId].extract) {
          return response.data.query.pages[pageId].extract;
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Description could not be retrieved");
      }
    }

    factory.getPageImagesList = function(pageId) {
      return $http
        .jsonp(buildPageImagesListQuery(pageId))
        .then(onSuccess, onError);

      function onSuccess(response) {
        var acceptedImageExtensions = ['bmp', 'eps', 'gif', 'jpeg', 'jpg',
                                      'pcd', 'pct', 'pdf', 'pict', 'png',
                                      'ps', 'psd', 'pub', 'svg', 'thm'];
                                      
        var blackListedImages = ['commons-logo.svg', 'ambox_scales.svg', 'ambox_current_red.svg',
                                  'star_full.svg', 'star_empty.svg', 'searchtool.svg',
                                  'crystal_clear_app_browser.png', 'padlock.svg', 'padlock-silver.svg',
                                  'padlock-olive.svg', 'ambox_outdated_serious.svg', 'wikinews-logo.svg'];

        if (response
            && response.data
            && response.data.query
            && response.data.query.pages) {
          var imageObjects = {};
          angular.forEach(response.data.query.pages, function(page) {
            if (page.imageinfo
                && page.imageinfo.length > 0
                && page.imageinfo[0].url
                && page.imageinfo[0].width
                && page.imageinfo[0].height) {
              var url = page.imageinfo[0].url;
              var width = page.imageinfo[0].width;
              var height = page.imageinfo[0].height;
              if (!imageObjects.hasOwnProperty(url)) {
                if (100 <= width && width <= 1000 && 100 <= height && height <= 1000) {
                  var urlLowerCase = url.toLowerCase();
                  var isBlackListed = false;
                  angular.forEach(blackListedImages, function(blackListedImage) {
                    if (urlLowerCase.indexOf(blackListedImage) !== -1) {
                      isBlackListed = true;
                    }
                  });
                  if (!isBlackListed) {
                    var extension = urlLowerCase.split('.').pop();
                    if (acceptedImageExtensions.indexOf(extension) !== -1) {
                      imageObjects[url] = {
                        url: url,
                        width: width,
                        height: height
                      };
                    }
                  }
                }
              }
            }
          });
          var imageList = [];
          angular.forEach(imageObjects, function(imageObject) {
            imageList.push(imageObject);
          })
          return imageList;
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Images list could not be retrieved");
      }
    }

    factory.getPageThumbnail = function(pageId) {
      return $http
        .jsonp(buildPageThumbnailQuery(pageId))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (response
            && response.data
            && response.data.query
            && response.data.query.pages
            && response.data.query.pages[pageId]
            && response.data.query.pages[pageId].thumbnail
            && response.data.query.pages[pageId].thumbnail.source) {
          return response.data.query.pages[pageId].thumbnail.source;
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Thumbnail could not be retrieved");
      }
    }

    factory.getPageTitle = function(pageId) {
      return $http
        .jsonp(buildPageBasicDataQuery(pageId))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (response
            && response.data
            && response.data.query
            && response.data.query.pages
            && response.data.query.pages[pageId]
            && response.data.query.pages[pageId].title) {
          return response.data.query.pages[pageId].title;
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Title could not be retrieved");
      }
    }

    factory.search = function(query) {
      return $http
        .jsonp(buildSearchQuery(query))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (response
            && response.data
            && response.data.query
            && response.data.query.search
            && angular.isArray(response.data.query.search)) {
          var results = response.data.query.search;
          var pageIds = [], wrappedPromises = [];
          angular.forEach(results, function(result) {
            if (angular.isDefined(result.title)) {
              var wrappedPromise = factory.getPageIdByTitle(result.title)
                  .then(function(pageId) {
                    return factory.getPageDescription(pageId)
                        .then(function(pageDescription) {
                          // TODO: improve disambiguation page detection logic.
                          /* Only add if description is not too short. */
                          if (pageDescription.length > 50) {
                            pageIds.push(pageId);
                          }
                        });
                  })
                  .catch(function() {
                    /* Skip if pageId cannot be retrieved. */
                  });
              wrappedPromises.push(wrappedPromise);
            }
          })
          return $q.all(wrappedPromises).then(function() {
            return pageIds;
          });
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Query results could not be retrieved");
      }
    }

    factory.getPageIdByTitle = function(title) {
      return $http
        .jsonp(buildPageIdByTitleQuery(title))
        .then(onSuccess, onError);

      function onSuccess(response) {
        if (response
            && response.data
            && response.data.query
            && response.data.query.pages) {
          var pageIdArray = Object.keys(response.data.query.pages);
          if (pageIdArray.length > 0) {
            return pageIdArray[0];
          }
        }
        onError();
      }

      function onError() {
        throwWikipediaAPIError("Page Id could not be retrieved");
      }
    }

    return factory;

    function buildPageCategoriesQuery(pageId) {
      var queryTemplate = '';
      queryTemplate += 'https://{{wikipediaId}}.wikipedia.org/w/api.php?';
      queryTemplate += '&pageids={{pageId}}';
      queryTemplate += '&action=query';
      queryTemplate += '&prop=categories';
      queryTemplate += '&clshow=!hidden';
      queryTemplate += '&format=json';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        wikipediaId: yaviConfig.wikipediaId,
        pageId: pageId
      });
    }

    function buildPageBasicDataQuery(pageId) {
      var queryTemplate = '';
      queryTemplate += 'https://{{wikipediaId}}.wikipedia.org/w/api.php?';
      queryTemplate += '&pageids={{pageId}}';
      queryTemplate += '&action=query';
      queryTemplate += '&prop=extracts';
      queryTemplate += '&exintro=';
      queryTemplate += '&explaintext=';
      queryTemplate += '&format=json';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        wikipediaId: yaviConfig.wikipediaId,
        pageId: pageId
      });
    }

    function buildPageImagesListQuery(pageId) {
      var queryTemplate = '';
      queryTemplate += 'https://{{wikipediaId}}.wikipedia.org/w/api.php?';
      queryTemplate += '&pageids={{pageId}}';
      queryTemplate += '&action=query';
      queryTemplate += '&generator=images';
      queryTemplate += '&prop=imageinfo';
      queryTemplate += '&iiprop=url|dimensions';
      queryTemplate += '&gimlimit=10';
      queryTemplate += '&format=json';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        wikipediaId: yaviConfig.wikipediaId,
        pageId: pageId
      });
    }

    function buildPageThumbnailQuery(pageId) {
      var queryTemplate = '';
      queryTemplate += 'https://{{wikipediaId}}.wikipedia.org/w/api.php?';
      queryTemplate += '&pageids={{pageId}}';
      queryTemplate += '&action=query';
      queryTemplate += '&prop=pageimages';
      queryTemplate += '&pithumbsize=500';
      queryTemplate += '&format=json';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        wikipediaId: yaviConfig.wikipediaId,
        pageId: pageId
      });
    }

    function buildSearchQuery(query) {
      var queryTemplate = '';
      queryTemplate += 'https://{{wikipediaId}}.wikipedia.org/w/api.php?';
      queryTemplate += '&action=query';
      queryTemplate += '&list=search';
      queryTemplate += '&srsearch={{query}}';
      queryTemplate += '&srnamespace=0';
      queryTemplate += '&srwhat=text';
      queryTemplate += '&srprop=size';
      queryTemplate += '&indexpageids=';
      queryTemplate += '&redirects=';
      queryTemplate += '&srlimit=20';
      queryTemplate += '&format=json';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        wikipediaId: yaviConfig.wikipediaId,
        query: query
      });
    }

    function buildPageIdByTitleQuery(title) {
      var queryTemplate = '';
      queryTemplate += 'https://{{wikipediaId}}.wikipedia.org/w/api.php?';
      queryTemplate += '&titles={{title}}';
      queryTemplate += '&action=query';
      queryTemplate += '&format=json';
      queryTemplate += '&callback=JSON_CALLBACK';
      return $interpolate(queryTemplate)({
        wikipediaId: yaviConfig.wikipediaId,
        title: title
      });
    }

    function throwWikipediaAPIError(errorMessage) {
      throw $interpolate("Wikipedia API: {{errorMessage}}.")({errorMessage: errorMessage});
    }
  }
  
})();

(function() {
  'use strict';

  angular
    .module('yavi')
    .controller('PageNotFoundController', controllerFunction);

  /** @ngInject */
  function controllerFunction(yvPageTitleConfig, $state) {
    var vm = this;

    yvPageTitleConfig.setDefaultPageTitle();

    vm.stateToHome = function() {
      $state.go('home');
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('yavi')
    .controller('ExplorePageController', controllerFunction);

  /** @ngInject */
  function controllerFunction(wikipediaPages, $stateParams, yvPageTitleConfig, $state, $location) {
    var vm = this;

    if (!wikipediaPages.isValidId($stateParams.pageId)) {
      $state.go('pageNotFound');
    }

    //
    vm.page = wikipediaPages.getPageById($stateParams.pageId);
    vm.period = {
      startDate: $stateParams.startDate || '2014-01-01',
      endDate: $stateParams.endDate || '2014-03-01'
    };

    vm.page.getTitle().then(function(title) {
      yvPageTitleConfig.setPageTitle(title);
    }).catch(function() {
      $state.go('pageNotFound');
    });

    vm.absoluteURL = $location.absUrl();
  }

})();

